<template lang="pug">
    .content
      .row
        .col-md-12
          .col-md-8
            .col-md-4.nopadding
              md-fg-input(label='Tên món', :disabled='false', :labelFloating='false', v-model='name')
            .col-md-4.nopadding-right
              md-fg-input(label='Giá', :disabled='false', :labelFloating='false', v-model='price')
            .col-md-4.nopadding-right
              md-fg-input(label='Số phần', :disabled='false', :labelFloating='false', v-model='remainingDishes')
            .col-md-12.nopadding
              md-fg-input(type='textarea', label='Mô tả',:rows='5', :disabled='false', :labelFloating='false', v-model='subtitle')
          .col-md-4.imagedish
            p Hình ảnh
            .image-avatar
              label.content-upload(for='upload-image-dish')
                img.img(:src='imageNewDish', for='upload-image-dish')
                md-fg-input-file(id='upload-image-dish', :disabled='false', :labelFloating='false', @uploadFile='handleUploadFile')
        .col-md-12.mg-t-20
          .col-md-12
            .mg-r-10.md-button.btn.btn-primary(@click='editDish') Lưu
            .mg-r-10.md-button.btn.btn-normal(@click='cancelEdit') Hủy




</template>

<script>
  import axios from 'axios'
  import swal from 'sweetalert'

  export default {
    name: 'md-editdish',
    data() {
      return {
        id: 0,
        name: '',
        price: 0,
        subtitle: '',
        imageDish: '',
        imageDishDefault: 'https://restaurantchatbot.s3.amazonaws.com/1541085716573-noimage.jpg',
        orderTime: 0,
        remainingDishes: 0,
        defaultRemainingDishes: 0,
        res: {}
      }
    },
    computed: {
      imageNewDish() {
        if (this.imageDish !== '') {
          return this.imageDish
        } else {
          return this.imageDishDefault
        }
      }
    },
    mounted() {
      var id = this.$route.params.id

      if (id) {
        var token = window.localStorage.getItem('user_token')
        axios.get('admin/dish/get/' + id.toString(), {
          headers: {Authorization: token}
        })
          .then(response => {
            this.res = response.data
            this.name = response.data.title
            this.price = response.data.price
            this.subtitle = response.data.subTitle
            this.imageDish = response.data.imageUrl
            this.orderTime = response.data.orderTimes
            this.remainingDishes = response.data.remainingDishes
            this.defaultRemainingDishes = response.data.defaultRemainingDishes
          })
      }
    },
    methods: {
      handleUploadFile(val) {
        var self = this
        var token = window.localStorage.getItem('user_token')
        let formData = new FormData()
        formData.append('file', val)
        axios.post('/upload',
          formData,
          {
            headers: {
              'Content-Type': 'multipart/form-data',
              Authorization: token
            }
          })
          .then(response => {
            self.imageDish = response.data
          })
      },
      editDish() {
        var id = this.$route.params.id
        var token = window.localStorage.getItem('user_token')
        axios.put('/admin/dish/update/' + id, {
          'imageUrl': this.imageDish,
          'price': parseFloat(this.price),
          'subTitle': this.subtitle,
          'title': this.name,
          'defaultRemainingDishes': this.defaultRemainingDishes,
          'remainingDishes': this.remainingDishes
        }, {
          headers: {Authorization: token}
        })
          .then(response => {
            this.name = response.data.title
            this.price = response.data.price
            this.subtitle = response.data.subTitle
            this.imageDish = response.data.imageUrl
            this.orderTime = response.data.orderTimes
            this.remainingDishes = response.data.remainingDishes
            this.defaultRemainingDishes = response.data.defaultRemainingDishes
            swal('Thành công!', 'Đã chỉnh sửa thành công!', 'success')
          })
      },
      cancelEdit() {
        this.$router.push('/menu')
      }
    }
  }
</script>

<style lang="scss"></style>



