<template lang="pug">
#content(v-if="logged")
  .wrapper(:class="{ 'nav-open' : navOpened }")
    sidebar(:open='navOpened')
    md-notifications
    .main-panel
      navigation(@toggleSideBar='toggleSideBar')
      router-view
      md-footer
      .close-layer(v-if='navOpened', @click='onCloseLayerClick')
#content(v-else)
  .wrapper
    login(@logginSuccess='logginSuccess')


</template>

<script>
  import Navigation from '@/components/Navigation'
  import MdFooter from '@/components/Footer'
  import Sidebar from '@/components/Sidebar'
  import Login from '@/components/Login'
  import axios from 'axios'

  export default {
    name: 'app',
    components: {
      Navigation,
      MdFooter,
      Sidebar,
      Login
    },

    data() {
      return {
        sidebarToggled: false,
        logged: false,
        listUser: {}
      }
    },
    computed: {
      navOpened() {
        return this.sidebarToggled
      }
    },

    mounted() {
      this.initMaterial()
      this.checkToken()
    },

    updated() {
      this.initMaterial()
    },

    methods: {
      checkToken() {
        var token = window.localStorage.getItem('user_token')
        if (token) {
          this.$store.commit('setUserToken', token)
          this.logginSuccess()
        } else {
          this.logged = false
        }
      },
      initMaterial() {
        $.material.init() // eslint-disable-line
      },
      toggleSideBar() {
        this.sidebarToggled = !this.sidebarToggled
      },
      onCloseLayerClick() {
        this.toggleSideBar()
      },
      logginSuccess() {
        this.getUserNameFromToken()
        this.getListUser()
        this.getUserInfor()
        this.logged = true
      },
      getListUser() {
        var self = this
        axios.get('/users/list/?offset=0&pageNumber=0&pageSize=20',
          {
            headers: {
              'Content-Type': 'multipart/form-data',
              Authorization: self.$store.state.userToken
            }
          })
          .then(response => {
            self.listUser = response.data.content
            self.getUserInfor()
          })
      },
      getUserInfor() {
        var self = this
        if (self.listUser.length > 0) {
          var user = self.listUser.filter((user) => {
            return user.userName === self.$store.state.userName
          })
          if (user) {
            self.$store.commit('setUserInfo', user[0])
          }
        }
      },
      getUserNameFromToken() {
        if (this.$store.state.userToken) {
          var user = this.jwtDecode(this.$store.state.userToken)
          if (user) {
            this.$store.commit('setUserName', user.sub)
          }
        }
      },
      jwtDecode(val) {
        var pos = val.lastIndexOf(' ')
        var token = val.substring(pos + 1, val.length)
        var base64Url = token.split('.')[1]
        var base64 = base64Url.replace('-', '+').replace('_', '/')
        return JSON.parse(window.atob(base64))
      }
    }
  }
</script>

<style lang="scss">
  @import "./assets/sass/md/_variables.scss";

  .table {
    thead {
      th {
        font-weight: $headings-font-weight;
      }
    }
  }

  @media (min-width: 992px) {
    .typo-line {
      padding-left: 140px;
      margin-bottom: 40px;
      position: relative;
      .category {
        transform: translateY(-50%);
        top: 50%;
        left: 0px;
        position: absolute;
      }
    }
  }

  .places-buttons .btn {
    margin-bottom: 30px
  }

  .space-70 {
    height: 70px;
    display: block;
  }

  .sidebar .nav > li.active-pro {
    position: absolute;
    width: 100%;
    bottom: 10px;
  }

  .tim-row {
    margin-bottom: 20px;
    padding-top: 50px;
    h3 {
      margin-top: 0;
    }
  }

  .tim-typo {
    padding-left: 25%;
    margin-bottom: 40px;
    position: relative;
    .tim-note {
      bottom: 10px;
      color: #c0c1c2;
      display: block;
      font-weight: 400;
      font-size: 13px;
      line-height: 13px;
      left: 0;
      margin-left: 20px;
      position: absolute;
      width: 260px;
    }
  }
</style>




