<template lang="pug">
  transition(name="modal")
    .modal-mask(v-show="showModal", :id="id" )
      .modal-wrapper
        .modal-container
          .modal-body
            slot(name="body")


</template>


<script>
  export default {
    name: 'md-modal',
    props: {
      id: '',
      value: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        showModal: false
      }
    },
    watch: {
      value: function (val) {
        this.showModal = val
        if (val) {
          window.$q('#' + this.id).appendTo('body')
        }
      }
    },
    methods: {
      loggin() {
        this.$emit('closeModal')
      }
    }

  }
</script>




