<template lang="pug">
  .list-dish-category-wrapper
    .item(v-for='dish in d_listDish')
      md-dish(:dishObj='dish', @delDish="delDish", @editDish='editDish', :categoryId='categoryId')
    md-addnewdish(:categoryId="categoryId", @addnewdish="addNewDish")

</template>

<script>
  export default {
    name: 'md-listdish',
    props: {
      listDish: {
        type: Array
      },
      categoryId: {
        type: Number
      }
    },
    data() {
      return {
        d_listDish: []
      }
    },
    watch: {
      listDish: function (val) {
        this.d_listDish = val
      }
    },
    methods: {
      delDish(id, categoryId) {
        this.$emit('delDish', id, categoryId)
      },
      addNewDish(categoryId) {
        this.$emit('addNewDish', categoryId)
      },
      editDish(dishObj, categoryId) {
        this.$emit('editDish', dishObj, categoryId)
      }
    },
    mounted() {
      this.d_listDish = this.listDish
    }
  }

</script>

<style lang="scss"></style>



