<template lang="pug">
li.item-link-order(@click='clickItemStatus', :class="{'active' : active == name}")
  .wrap
    span {{title}}



</template>

<script>
  export default {
    props: {
      title: String,
      active: String,
      name: String
    },
    data() {
      return {
      }
    },
    methods: {
      clickItemStatus() {
        var self = this
        self.$emit('setActiveItem', self.name)
      }
    }
  }
</script>



