<template lang="pug">
.card(:class="cardClass")
  .card-header.row(:data-background-color="backgroundColor" v-if="iconRight")
    .col-md-10
      h4.title(v-if="!hasHeaderSlot") {{objectCategory.name}}
      p.category(v-if="!hasHeaderSlot") {{objectCategory.description}}
    .col-md-2
      .button-wrapper
        button.btn.btn-success.btn-xs(type='button', rel='tooltip', title='Edit Category', @click='editCategory')
          i.material-icons edit
        button.btn.btn-danger.btn-xs(type='button', rel='tooltip', title='Delete Category', @click='delCategory')
          i.material-icons delete
    slot(name='header')
  .card-header.row(:data-background-color="backgroundColor" v-else)
    h4.title(v-if="!hasHeaderSlot") {{objectCategory.name}}
      p.category(v-if="!hasHeaderSlot") {{objectCategory.description}}
    slot(name='header')
  .card-content(:class="{ 'table-responsive' : hasTable }")
    slot


</template>

<script>

  export default {
    name: 'md-card',
    props: {
      hasTable: Boolean,
      backgroundColor: String,
      cardClass: String,
      iconRight: {
        type: Boolean,
        default: false
      },
      objectCategory: {
        type: Object,
        default: {
          name: '',
          description: ''
        }
      }
    },
    computed: {
      hasHeaderSlot() {
        return !!this.$slots['header']
      }
    },
    methods: {
      editCategory() {
        this.$emit('editCategory', this.objectCategory)
      },
      delCategory() {
        this.$emit('delCategory', this.objectCategory)
      }
    }
  }
</script>

<style lang="scss">
  .button-wrapper {
    text-align: center;
    .btn {
      margin-left: 5px;
      margin-right: 5px;
      padding-top: 8px !important;
      padding-bottom: 8px !important;
    }
  }
</style>



