<template lang="pug">
.content
  md-table(class='table-user', type='hover', :data='table.data', :columns='table.columns')


</template>
<script>

  import axios from 'axios'

  export default {
    data() {
      return {
        table: {
          columns: ['ID_ORDER', 'NAME', 'FEEDBACK', 'RATING'],
          data: []
        },
        allfeedback: []
      }
    },
    methods: {
      getAllFeedback() {
        var self = this
        return axios.get('/admin/order/feedback',
          {
            headers: {
              'Content-Type': 'multipart/form-data',
              Authorization: self.$store.state.userToken
            }
          })
          .then(response => {
            self.allfeedback = response.data
          })
      },
      formatDataFeedback() {
        var self = this
        // var test = []
        var rootData = Object.assign({}, self.allfeedback)
        if (rootData.content) {
          var res = rootData.content.map((cus, index) => {
            if (cus.rating != null) {
              return {
                'id_order': cus.orderId,
                'name': cus.customer.lastName + ' ' + cus.customer.firstName,
                'feedback': cus.feedback,
                'rating': cus.rating
              }
            }
          }).filter((elms) => {
            return elms !== undefined
          })
          this.table.data = res
        }
      }
    },
    mounted() {
      this.getAllFeedback().then(res => {
        this.formatDataFeedback()
      })
    }
  }

</script>



