<template lang="pug">
  .login-wrapper
      .col-md-4.col-md-offset-4.form-login
          .title
            span ĐĂNG NHẬP
          md-fg-input(label='Tên đăng nhập', :disabled='false', v-model='user')
          md-fg-input(label='Mật khẩu',type="password" :disabled='false', v-model='pass')
          .button-wrapper
            md-button.btn.btn-primary(@click='loggin') Đăng nhập
            p {{ error }}


</template>

<script>
  export default {
    name: 'md-login',
    data() {
      return {
        user: '',
        pass: '',
        error: ''
      }
    },
    methods: {
      loggin() {
        var app = this
        if (app.validateForm()) {
          this.$auth.login({
            fetchUser: false,
            url: 'login',
            data: {userName: app.user, password: app.pass},
            rememberMe: true,
            success: (response) => {
              window.localStorage.setItem('user_token', response.data.Authorization)
              app.$store.commit('setUserToken', response.data.Authorization)
              app.$emit('logginSuccess', response.data.Authorization)
            },
            error: () => {
              this.error = 'Tên đăng nhập hoặc mật khẩu không đúng !'
            }
            // redirect: {name: 'Home'}
          })
        }
      },
      validateForm() {
        return true
      }

    }
  }
</script>

<style lang="scss"></style>



