<template lang="pug">
    .content
      .row
        .col-md-12
          .col-md-8
            .col-md-4.nopadding
              md-fg-input(label='Tên món', :disabled='false', :labelFloating='false', v-model='name')
            .col-md-4.nopadding-right
              md-fg-input(label='Giá', :disabled='false', :labelFloating='false', v-model='price')
            .col-md-4.nopadding-right
              md-fg-input(label='Số phần', :disabled='false', :labelFloating='false', v-model='remainingDishes')
            .col-md-12.nopadding
              md-fg-input(type='textarea', label='Mô tả',:rows='5', :disabled='false', :labelFloating='false', v-model='subtitle')
          .col-md-4.imagedish
            p Hình ảnh
            .image-avatar
              label.content-upload(for='upload-image-dish')
                img.img(:src='imageNewDish', for='upload-image-dish')
                md-fg-input-file(id='upload-image-dish', :disabled='false', :labelFloating='false', @uploadFile='handleUploadFile')
        .col-md-12.mg-t-20
          .col-md-12
            .mg-r-10.md-button.btn.btn-primary(@click='addDish') Lưu
            .mg-r-10.md-button.btn.btn-normal(@click='cancelDish') Hủy

</template>

<script>
  import axios from 'axios'
  import swal from 'sweetalert'

  export default {
    name: 'md-addnewdish',
    data() {
      return {
        name: '',
        price: 0,
        subtitle: '',
        imageDish: '',
        remainingDishes: 0,
        imageDishDefault: 'https://restaurantchatbot.s3.amazonaws.com/1541085716573-noimage.jpg'
      }
    },
    computed: {
      imageNewDish() {
        if (this.imageDish !== '') {
          return this.imageDish
        } else {
          return this.imageDishDefault
        }
      }
    },
    methods: {
      handleUploadFile(val) {
        var self = this
        var token = window.localStorage.getItem('user_token')
        let formData = new FormData()
        formData.append('file', val)
        axios.post('/upload',
          formData,
          {
            headers: {
              'Content-Type': 'multipart/form-data',
              Authorization: token
            }
          })
          .then(response => {
            // this.imageDish = response
            self.imageDish = response.data
          })
      },
      addDish() {
        var self = this
        var token = window.localStorage.getItem('user_token')
        axios.post('/admin/dish/add', {
          'imageUrl': this.imageDish,
          'price': parseFloat(this.price),
          'subTitle': this.subtitle,
          'remainingDishes': this.remainingDishes,
          'title': this.name
        }, {
          headers: {Authorization: token}
        })
          .then(function (response) {
            swal('Đã thêm!', 'Đã thêm món ăn thành công')
              .then(ok => {
                self.$router.push('/menu')
              })
          })
      },
      cancelDish() {
        var self = this
        self.$router.push('/menu')
      }
    }
  }
</script>

<style lang="scss"></style>



