<template lang="pug">
.content
  md-table(type='hover table-customer', :data='table.data', :columns='table.columns')



</template>
<script>

  import axios from 'axios'

  export default {
    data() {
      return {
        table: {
          columns: ['ID', 'NAME', 'GENDER', 'EMAIL', 'ADDRESS', 'CHAT'],
          data: []
        },
        allcustomer: {},
        vailua: []
      }
    },
    methods: {
      getAllCustomer() {
        var self = this
        var token = window.localStorage.getItem('user_token')

        axios.get('/admin/customer/list/?offset=0&pageNumber=0&pageSize=20',
          {
            headers: {
              'Content-Type': 'multipart/form-data',
              Authorization: token
            }
          })
          .then(response => {
            self.allcustomer = response.data
            self.formatDataCustomter()
          })
      },
      formatDataCustomter() {
        var self = this
        // var test = []
        var rootData = Object.assign({}, self.allcustomer)
        if (rootData.content) {
          var test1 = rootData.content.map((cus, index) => {
            return {
              'id': index,
              'name': cus.firstName + ' ' + cus.lastName,
              'image': cus.profilePicUrl,
              'messengerId': cus.messengerUserId,
              'email': cus.email,
              'address': cus.address,
              'gender': cus.gender,
              'chat': '<a class="icon-chat-customer" target="_blank" href="https://www.facebook.com/messages/t/' + cus.messengerUserId.toString() + '"><i class="fab fa-facebook-messenger"></i></a>'
            }
          })
          this.vailua = test1
          this.table.data = test1
        }
      }

    },
    mounted() {
      this.getAllCustomer()
    }
  }

</script>



