<template lang="pug">
ul.nav(:class='navClasses')
  li.dropdown(:class='{ open : isDropdownNotiOpened }')
    a.dropdown-toggle(href='#', data-toggle='dropdown', @click.prevent='openNotiDropdown')
      i.material-icons notifications
      span.notification 5
      p.hidden-lg.hidden-md Notifications
    ul.dropdown-menu
      li
        a(href='#') Bạn có một đơn đặt hàng mới
      li
        a(href='#') Bạn có một đơn đặt hàng mới
      li
        a(href='#') Khách hàng đã đánh giá cửa hàng
      li
        a(href='#') Bạn có một đơn đặt hàng mới
      li
        a(href='#') Khách hàng đã hủy đơn hàng

  li.dropdown(:class='{ open : isDropdownProfileOpened }', v-click-outside='closeProfileDropdown')
    a.dropdown-toggle(href='#', data-toggle='dropdown', @click.prevent='openProfileDropdown')
      i.material-icons person
      span {{name}}
    ul.dropdown-menu
      li
        a(@click='oprenModalInforProfile') Thông tin người dùng
      li
        a(@click='logOut') Đăng xuất

  md-modal(id='modal-infor-profile', v-model='showModal', @closeModal='closeModalInforProfile')
    h3(slot='header' class='display-none') Thông tin người dùng
    .row(slot='body')
      .col-md-12
        .card
          .card-header(data-background-color='purple')
            h4.title Xem thông tin người dùng

          .card-content
            form.max-width
              .row
                .col-md-6
                  md-fg-input(label='Username', :disabled='true', :labelFloating='false', v-model='user.userName')
                .col-md-6
                  md-fg-input(label='Email', :disabled='!isEdit', :labelFloating='false', v-model='user.email')
              .row
                .col-md-12
                  md-fg-input(label='Adress', :disabled='!isEdit', :labelFloating='false', v-model='user.address')
              .row
                .col-md-6
                  md-fg-input(label='Phone', :disabled='!isEdit' :labelFloating='false', v-model='user.phoneNumber')
                .col-md-6
                  .col-md-12
                    span role
                  .col-md-6
                    input(type='radio',value='1', id='one', v-model='user.roles[0].id', :disable='!isEdit')
                    label(for='one') Admin
                  .col-md-6
                    input(type='radio',value='2', id='two', v-model='user.roles[0].id', :disable='!isEdit')
                    label(for='two') Staff

              md-button.btn.pull-right(@click.prevent='closeModalInforProfile') Đóng
              md-button.btn.btn-primary.pull-right(@click.prevent='startEditProfile', v-show='!isEdit') Chỉnh Sửa
              md-button.btn.btn-success.pull-right(@click.prevent='changeProfile', v-show='isEdit') OK
              .clearfix





</template>
<script>
  import axios from 'axios'
  import swal from 'sweetalert'

  export default {
    name: 'navbar-right',
    props: {
      mobile: Boolean
    },
    data() {
      return {
        isEdit: false,
        user: {
          id: 0,
          userName: '',
          address: '',
          email: '',
          phoneNumber: '',
          roles: [{
            id: 1,
            roleName: 'One',
            description: 'string'
          }]
        },
        // userInfo: {},
        showModal: false,
        name: this.$store.state.userName,
        isDropdownNotiOpened: false,
        isDropdownProfileOpened: false
      }
    },
    computed: {
      navClasses() {
        return this.mobile ? ['nav-mobile-menu'] : ['navbar-nav', 'navbar-right']
      }
    },
    methods: {
      startEditProfile() {
        this.isEdit = true
      },
      changeProfile() {
        var self = this
        axios.post('/users/update-profile', {
          'address': self.user.address,
          'email': self.user.email,
          'phoneNumber': self.user.phoneNumber,
          'userName': self.user.userName
        }, {
          headers: {Authorization: self.$store.state.userToken}
        })
          .then(function (response) {
            swal('Thành công!', 'Đã chỉnh sửa thành công!', 'success')
          })
      },
      closeModalInforProfile() {
        this.showModal = false
      },
      oprenModalInforProfile() {
        this.initUserInfor()
        this.showModal = true
      },
      openNotiDropdown() {
        this.isDropdownNotiOpened = !this.isDropdownNotiOpened
        this.closeProfileDropdown()
      },
      closeNotiDropdown() {
        if (this.isDropdownNotiOpened) {
          this.isDropdownNotiOpened = false
        }
      },
      openProfileDropdown() {
        this.isDropdownProfileOpened = !this.isDropdownProfileOpened
      },
      closeProfileDropdown() {
        if (this.isDropdownProfileOpened) {
          this.isDropdownProfileOpened = false
        }
      },
      initUserInfor() {
        this.user = this.$store.state.userInfo
      },
      logOut() {
        window.localStorage.setItem('user_token', '')
        location.reload(true)
      }

    }
  }
</script>




