<template lang='pug'>
  .list-dish
    img.img(:src='dishObj.imageUrl')
    .content-wrapper
      .title
        .p-title
          span {{dishObj.title}}
        .date-cre
          span Giá: {{dishObj.price}}
      .button-wrapper
          i.material-icons(@click='editDish') edit
          i.material-icons(@click='delDish') delete


</template>

<script>
  import swal from 'sweetalert'
  import axios from 'axios'

  export default {
    name: 'md-dish',
    props: {
      dishObj: {
        type: Object,
        default: {
          'id': -1,
          'title': '',
          'imageUrl': '',
          'subTitle': '',
          'price': 0,
          'orderTimes': -1,
          'remainingDishes': 0,
          'defaultRemainingDishes': 0
        }
      },
      categoryId: {
        type: Number,
        default: -1
      }
    },
    methods: {
      editDish() {
        this.$emit('editDish', this.dishObj, this.categoryId)
      },
      delDish() {
        var self = this
        if (self.dishObj.id !== -1) {
          swal({
            title: 'Cảnh báo?',
            text: 'Món ăn đã xóa sẽ không khôi phục dữ liệu lại được!',
            icon: 'warning',
            buttons: [
              'Không',
              'Có'
            ],
            dangerMode: true
          })
            .then(willDelete => {
              if (willDelete) {
                axios.delete('/admin/dish/delete/' + self.dishObj.id.toString(), {
                  headers: {Authorization: self.$store.state.userToken}
                })
                  .then(function (response) {
                    self.$emit('delDish', self.dishObj.id, self.categoryId)
                    swal('Đã xóa!', 'Đã xóa món ăn thành công', 'success')
                  })
              }
            })
        }
      }
    },
    filters: {
      dateTimeFormat: function (value) {
        var today = new Date(value)
        var dd = today.getDate()
        var mm = today.getMonth() + 1

        var yyyy = today.getFullYear()
        if (dd < 10) {
          dd = '0' + dd
        }
        if (mm < 10) {
          mm = '0' + mm
        }
        var todayr = dd + '/' + mm + '/' + yyyy
        return todayr
      }
    }
  }
</script>

<style lang='scss'></style>



