<template lang="pug">
.form-group.upload(:class="{ 'label-floating' : labelFloating }")
  input.form-control(v-bind='$props',:id='id', ref='file', v-on:change="handleFileUpload()")



</template>
<script>
  export default {
    name: 'md-fg-input-file',
    props: {
      type: {
        type: String,
        default: 'file'
      },
      name: String,
      disabled: Boolean,
      placeholder: String,
      value: [String, Number],
      labelFloating: Boolean,
      rows: Number,
      cols: Number,
      id: String
    },
    data() {
      return {
        file: ''
      }
    },
    methods: {
      handleFileUpload() {
        this.file = this.$refs.file.files[0]
        this.$emit('uploadFile', this.file)
      }
    }
  }

</script>
<style lang="scss"></style>



