<template lang="pug">
.content
  .md-button.btn.btn-success.pull-right(@click='addNewCategory') Thêm Category
  .row
    .col-md-12(v-for='list in list_category')
      md-card(:objectCategory='list', backgroundColor='purple', :iconRight='true', @editCategory='editCategory', @delCategory='delCategory')
        md-listdish(:listDish="list.dishes" :categoryId="list.id",@delDish='delDish', @addNewDish='addNewDish', @editDish='editDish')

  md-modal(id='modal-new-category', v-model='showModal', @closeModal='closeModalNewCategory')
    h3(slot='header' class='display-none') Thêm mới Category
    .row(slot='body')
      .col-md-12
        .card
          .card-header(data-background-color='purple')
            h4.title(v-if="mode == 'create'") Thêm mới Category
            h4.title(v-else) Chỉnh sửa Category

          .card-content
            form.max-width
              .row
                .col-md-12
                  md-fg-input(label='Tên category', :disabled='false', :labelFloating='false', v-model='category_temp.name')
                .col-md-12
                  md-fg-input(type='textarea', label='Mô tả', row='5', :disabled='false', :labelFloating='false', v-model='category_temp.description')

              md-button.btn.pull-right(@click.prevent='closeModalNewCategory') Đóng
              md-button.btn.btn-success.pull-right(@click.prevent='saveCreateCategory', v-if='mode=="create"') Thêm
              md-button.btn.btn-success.pull-right(@click.prevent='saveEditCategory', v-else) Chỉnh sửa
              .clearfix

  md-modal(id='modal-new-dish', v-model='showModalNewDish', @closeModal='closeModalNewDish')
    h3(slot='header' class='display-none') Thêm mới món ăn
    .row(slot='body')
      .col-md-12
        .card
          .card-header(data-background-color='purple')
            h4.title(v-if="modeNewDish == 'create'") Thêm mới món ăn
            h4.title(v-else) Chỉnh sửa món ăn

          .card-content
            form.max-width
              .row
                .col-md-12
                  .col-md-8
                    .col-md-6.nopadding.pd-r-10
                      md-fg-input(label='Tên món', :disabled='false', :labelFloating='false', v-model='dish_data_temp.title')
                    .col-md-6.nopadding.pd-r-10
                      md-fg-input(label='Giá', :disabled='false', :labelFloating='false', v-model='dish_data_temp.price')
                    .col-md-6.nopadding.pd-r-10
                      md-fg-input(label='Số phần còn lại', :disabled='false', :labelFloating='false', v-model='dish_data_temp.remainingDishes')
                    .col-md-6.nopadding.pd-r-10
                      md-fg-input(label='Số phần còn lại mặc định', :disabled='false', :labelFloating='false', v-model='dish_data_temp.defaultRemainingDishes')
                    .col-md-12.nopadding.pd-r-10
                      md-fg-input(type='textarea', label='Mô tả',:rows='4', :disabled='false', :labelFloating='false', v-model='dish_data_temp.subTitle')
                  .col-md-4.imagedish
                    p Hình ảnh
                    .image-avatar
                      label.content-upload(for='upload-image-dish')
                        img.img(:src='imageNewDish', for='upload-image-dish')
                        md-fg-input-file(id='upload-image-dish', :disabled='false', :labelFloating='false', @uploadFile='handleUploadFile')

              md-button.btn.pull-right(@click.prevent='closeModalNewDish') Đóng
              md-button.btn.btn-success.pull-right(@click.prevent='saveAddDish', v-if='modeNewDish=="create"') Thêm
              md-button.btn.btn-success.pull-right(@click.prevent='saveEditDish', v-else) Chỉnh sửa
              .clearfix




























</template>

<script>

  import axios from 'axios'
  import swal from 'sweetalert'

  export default {
    data() {
      return {
        // category
        list_category: [
          {
            name: '',
            description: ''
          }
        ],
        category_temp: {
          name: '',
          description: '',
          id: ''
        },
        showModal: false,
        mode: 'create',
        //
        // new dish
        showModalNewDish: false,
        modeNewDish: 'create',
        dish_data_temp: {
          'id': -1,
          'title': '',
          'imageUrl': '',
          'subTitle': '',
          'price': 0,
          'orderTimes': -1,
          'remainingDishes': 0,
          'defaultRemainingDishes': 0,
          'categoryId': -1
        },
        imageDishLoad: '',
        imageDishDefault: 'https://restaurantchatbot.s3.amazonaws.com/1541085716573-noimage.jpg',
        haha: {}
      }
    },
    computed: {
      imageNewDish() {
        if (this.imageDishLoad !== '') {
          return this.imageDishLoad
        } else {
          return this.imageDishDefault
        }
      }
    },
    methods: {
      delDish(id, categoryId) {
        var self = this
        var categoryLocal = self.list_category.find(x => {
          return (x.id = categoryId)
        })
        var newLishDish = categoryLocal.dishes.filter((dish) => {
          return dish.id !== id
        })
        categoryLocal.dishes = newLishDish
      },
      getListCategory() {
        var self = this
        axios
          .get('/admin/category/list/', {
            headers: {Authorization: self.$store.state.userToken}
          })
          .then(response => (
            this.list_category = response.data.content
          ))
      },
      saveEditCategory() {
        var self = this
        axios.put('/admin/category/update/' + this.category_temp.id, {
          'name': self.category_temp.name,
          'description': self.category_temp.description,
          'imageUrl': 'https://restaurantchatbot.s3.amazonaws.com/1541085716573-noimage.jpg'
        }, {
          headers: {Authorization: self.$store.state.userToken}
        })
          .then(function (response) {
            swal('Thành công!', 'Chỉnh sửa thành công')
          })
      },
      closeModalNewCategory() {
        this.showModal = false
      },
      openModalNewCategory() {
        this.showModal = true
      },
      resetDataCategoryTemp() {
        var temp = {
          name: '',
          description: ''
        }
        this.category_temp = temp
      },
      addNewCategory() {
        this.mode = 'create'
        this.resetDataCategoryTemp()
        this.openModalNewCategory()
      },
      saveCreateCategory() {
        var self = this
        axios.post('/admin/category/add', {
          'name': self.category_temp.name,
          'description': self.category_temp.description,
          'imageUrl': 'https://restaurantchatbot.s3.amazonaws.com/1541085716573-noimage.jpg'
        }, {
          headers: {Authorization: self.$store.state.userToken}
        })
          .then(function (response) {
            swal('Đã thêm!', 'Đã thêm category thành công')
          })
      },
      editCategory(obj) {
        var self = this
        self.category_temp = obj
        self.mode = 'edit'
        self.openModalNewCategory()
      },
      delCategory(obj) {
        var self = this

        if (obj.dishes.length > 0) {
          swal('Không thành công!', 'Category của bạn còn các món ăn, bạn không thể xóa category này', 'error')
        } else {
          swal({
            title: 'Cảnh báo?',
            text: 'Bạn có chắc sẽ xóa category này?. Dữ liệu xóa sẽ không được phục hồi',
            icon: 'warning',
            buttons: [
              'Không',
              'Có'
            ],
            dangerMode: true
          })
            .then(willDelete => {
              if (willDelete) {
                axios.delete('/admin/category/delete/' + obj.id.toString(), {
                  headers: {Authorization: self.$store.state.userToken}
                })
                  .then(function (response) {
                    self.delCategoryClient(obj.id)
                    swal('Đã xóa!', 'Đã xóa category thành công', 'success')
                  })
              }
            })
        }
      },
      delCategoryClient(id) {
        var self = this
        var newLish = self.list_category.filter((category) => {
          return category.id !== id
        })
        if (newLish) {
          this.list_category = newLish
        }
      },
      //
      //
      // add new dish
      closeModalNewDish() {
        this.showModalNewDish = false
        this.dish_data_temp = {
          'id': -1,
          'title': '',
          'imageUrl': '',
          'subTitle': '',
          'price': 0,
          'orderTimes': -1,
          'remainingDishes': 0,
          'defaultRemainingDishes': 0,
          'categoryId': -1
        }
        this.imageDishLoad = ''
      },
      addNewDish(categoryId) {
        this.modeNewDish = 'create'
        this.dish_data_temp.categoryId = categoryId
        // this.resetDataDishTemp()
        this.openModalNewDish()
      },
      editDish(dishObj, categoryId) {
        this.modeNewDish = 'edit'
        this.dish_data_temp = dishObj
        this.dish_data_temp.categoryId = categoryId
        this.imageDishLoad = dishObj.imageUrl
        // this.resetDataDishTemp()
        this.openModalNewDish()
      },
      openModalNewDish() {
        this.showModalNewDish = true
      },
      handleUploadFile(val) {
        let self = this
        let formData = new FormData()
        formData.append('file', val)
        axios.post('/upload', formData, {
          headers: {
            'Content-Type': 'multipart/form-data',
            Authorization: self.$store.state.userToken
          }
        })
          .then(response => {
            self.imageDishLoad = response.data
          })
      },
      saveAddDish() {
        var self = this
        axios.post('/admin/dish/add', {
          'categoryId': self.dish_data_temp.categoryId,
          'imageUrl': self.imageNewDish,
          'price': parseFloat(self.dish_data_temp.price),
          'subTitle': self.dish_data_temp.subTitle,
          'remainingDishes': self.dish_data_temp.remainingDishes,
          'defaultRemainingDishes': self.dish_data_temp.defaultRemainingDishes,
          'title': self.dish_data_temp.title
        }, {
          headers: {Authorization: self.$store.state.userToken}
        })
          .then(function (response) {
            // add dish to list dish
            var temIndex = -1
            var categoryLocal = self.list_category.find((x, index) => {
              temIndex = index
              return (x.id = self.dish_data_temp.categoryId)
            })
            if (temIndex !== -1) {
              categoryLocal.dishes.push(response.data)
              self.list_category[temIndex] = categoryLocal
            }

            // self.$set(this.list_category, '')
            // self.haha = response.data
            swal('Đã thêm!', 'Đã thêm món ăn thành công')
              .then(ok => {
                self.closeModalNewDish()
              })
          })
      },
      saveEditDish() {
        var self = this
        axios.put('/admin/dish/update/' + self.dish_data_temp.id, {
          'imageUrl': self.imageNewDish,
          'price': parseFloat(self.dish_data_temp.price),
          'subTitle': self.dish_data_temp.subTitle,
          'title': self.dish_data_temp.title,
          'defaultRemainingDishes': self.dish_data_temp.defaultRemainingDishes,
          'remainingDishes': self.dish_data_temp.remainingDishes,
          'categoryId': self.dish_data_temp.categoryId
        }, {
          headers: {Authorization: self.$store.state.userToken}
        })
          .then(response => {
            this.dish_data_temp = response.data
            this.imageDishLoad = response.data.imageUrl
            swal('Thành công!', 'Đã chỉnh sửa thành công!', 'success')
          })
      }
    },
    created() {
      this.getListCategory()
    }
  }
</script>
<style lang="sass">

</style>



