<template lang="pug">
.content
    .row
      .col-md-12
        .mg-r-10.md-button.btn.btn-primary(@click='addNewPromotion') Thêm mới khuyến mãi
        .promotion-wrapper
          .promotion-item(v-for='pro_item in list_promotion')
            .image
              img.img(:src='pro_item.imageUrl')
            .text-wrapper
              .title {{ pro_item.title }}
              .from-date-to-date {{ pro_item.availableFrom|dateTimeFormat }}  -  {{ pro_item.availableTo|dateTimeFormat }}
              .percent-down Giảm {{ pro_item.percentDown }} %
              .button-wrapper
                i.material-icons.edit(@click="editPromote(pro_item)") edit
                i.material-icons.del(@click="delPromotion(pro_item)") delete

    modal-create-edit-promotion(:triggerShowModal="triggerOpenModalPromotion",
    :promotionData="promotion_data",
    :mode="mode_modal", @closeModalPromotion="closeModalPromotion")



</template>

<script>
  import axios from 'axios'
  import swal from 'sweetalert'
  import ModalCreateEditPromotion from '../ModalCreateEditPromotion/index.vue'

  export default {
    data() {
      return {
        list_promotion: [],
        triggerOpenModalPromotion: false,
        mode_modal: 'create',
        promotion_data: {}
      }
    },
    components: {
      ModalCreateEditPromotion
    },
    filters: {
      dateTimeFormat: function (value) {
        var today = new Date(value)
        var dd = today.getDate()
        var mm = today.getMonth() + 1

        var yyyy = today.getFullYear()
        if (dd < 10) {
          dd = '0' + dd
        }
        if (mm < 10) {
          mm = '0' + mm
        }
        var todayr = dd + '/' + mm + '/' + yyyy
        return todayr
      }
    },
    methods: {
      closeModalPromotion() {
        this.triggerOpenModalPromotion = false
      },
      getListPromotion() {
        var self = this

        axios.get('admin/promotion/list/', {
          headers: {Authorization: self.$store.state.userToken}
        })
          .then(response => {
            self.list_promotion = response.data.content
          })
      },
      addNewPromotion() {
        this.mode_modal = 'create'
        this.triggerOpenModalPromotion = true
      },
      editPromote(item) {
        this.promotion_data = item
        this.mode_modal = 'edit'
        this.triggerOpenModalPromotion = true
      },
      delPromotion(val) {
        var self = this
        swal({
          title: 'Cảnh báo?',
          text: 'Khuyến mãi sẽ không khôi phục dữ liệu lại được!',
          icon: 'warning',
          buttons: [
            'Không',
            'Có'
          ],
          dangerMode: true
        })
          .then(willDelete => {
            if (willDelete) {
              axios.delete('/admin/promotion/delete/' + val.id.toString(), {
                headers: {Authorization: self.$store.state.userToken}
              })
                .then(function (response) {
                  // self.$emit('delDish', self.id)
                  self.delPromotionTemp(val.id)
                  swal('Đã xóa!', 'Đã xóa khuyến mãi thành công', 'success')
                })
            }
          })
      },
      delPromotionTemp(id) {
        var self = this
        var newLish = self.list_promotion.filter((promote) => {
          return promote.id !== id
        })
        if (newLish) {
          this.list_promotion = newLish
        }
      }
    },

    created() {
      this.getListPromotion()
    }
  }
</script>
<style lang="sass">

</style>




