<template lang="pug">
table.table(:class='tableClass')
  thead
    tr
      th(v-for='c in columns') {{c}}
  tbody
    tr(v-for='item in data', @click="eventClickRow(item)")
      td(v-for='c in columns', v-if='hasValue(item, c)', v-html='itemValue(item, c)')


</template>

<script>
  export default {
    name: 'md-table',
    props: {
      columns: Array,
      data: Array,
      type: {
        type: String, // default | striped | hover
        default: ''
      },
      eventclickrow: {
        type: Boolean,
        default: false
      }
    },
    computed: {
      tableClass() {
        return `table-${this.type}`
      }
    },
    methods: {
      hasValue(item, column) {
        return item[column.toLowerCase()] !== 'undefined'
      },
      itemValue(item, column) {
        return item[column.toLowerCase()]
      },
      eventClickRow(item) {
        var self = this
        if (this.eventclickrow) {
          self.$emit('eventClickRow', item)
        }
      }
    }
  }

</script>
<style lang="scss"></style>



