<template lang="pug">
.content
  .row
    .col-md-2.order-status
      h4.status STATUS
      ul.nav
        item(title='ALL', :active='selectedItem', name='all', @setActiveItem="setActivationItem")
        <!--item(title='PENDING', :active='selectedItem', name='pending', @setActiveItem="setActivationItem")-->
        item(title='ORDERED', :active='selectedItem', name='ordered', @setActiveItem="setActivationItem")
        item(title='CONFIRMED', :active='selectedItem', name='confirmed', @setActiveItem="setActivationItem")
        item(title='DELIVERING', :active='selectedItem', name='delivering', @setActiveItem="setActivationItem")
        item(title='DELIVERED', :active='selectedItem', name='delivered', @setActiveItem="setActivationItem")
        item(title='CANCLED', :active='selectedItem', name='canceled', @setActiveItem="setActivationItem")
    .col-md-10
      md-table(class='table-order', type='hover', :data='table.data', :columns='table.columns', :eventclickrow="true", @eventClickRow="clickRow")

      div.text-center
        el-pagination(@size-change="handleSizeChange",@current-change="handleCurrentChange",
        :current-page.sync="currentPage", :page-size="sizePage",layout="prev, pager, next",:total="totalItemInPage")

  modal-order(:triggerShowModal="triggerOpenModalOrder", :orderID='orderInfor.id',:type="selectedItem", @closeModalOrder="closeModalOrder")
















</template>
<script>

  import axios from 'axios'
  // import swal from 'sweetalert'
  import Item from './item'
  import ModalOrder from '../ModalOrder/index.vue'

  export default {
    data() {
      return {
        type: 'order',
        table: {
          columns: ['ID', 'NAME', 'EMAIL', 'PRICE', 'TIMEORDER'],
          data: []
        },
        dataPending: [],
        selectedItem: 'ordered',
        triggerOpenModalOrder: false,
        orderInfor: {},
        currentPage: 1,
        sizePage: 10,
        totalItemInPage: 0
      }
    },
    components: {
      Item,
      ModalOrder
    },
    methods: {

      handleSizeChange(val) {
        console.log(`${val} items per page`)
      },
      handleCurrentChange(val) {
        this.formatDataTable()
      },
      getOrderAll() {
        var self = this
        axios.get('admin/order/list/', {
          headers: {Authorization: self.$store.state.userToken}
        })
          .then(response => {
            self.dataPending = response.data.content.reverse()
            self.totalItemInPage = self.dataPending.length
            self.formatDataTable()
          })
      },
      getOrderPendding() {
        var self = this
        axios.get('admin/order/list/?orderStatus=PENDING', {
          headers: {Authorization: self.$store.state.userToken}
        })
          .then(response => {
            self.dataPending = response.data.content.reverse()
            self.totalItemInPage = self.dataPending.length
            self.formatDataTable()
          })
      },
      getOrderOdered() {
        var self = this
        axios.get('admin/order/list/?orderStatus=ORDERED', {
          headers: {Authorization: self.$store.state.userToken}
        })
          .then(response => {
            self.dataPending = response.data.content.reverse()
            self.totalItemInPage = self.dataPending.length
            self.formatDataTable()
          })
      },
      getOrderConfirmed() {
        var self = this
        axios.get('admin/order/list/?orderStatus=CONFIRMED', {
          headers: {Authorization: self.$store.state.userToken}
        })
          .then(response => {
            self.dataPending = response.data.content.reverse()
            self.totalItemInPage = self.dataPending.length
            self.formatDataTable()
          })
      },
      getOrderDelivering() {
        var self = this
        axios.get('admin/order/list/?orderStatus=DELIVERING', {
          headers: {Authorization: self.$store.state.userToken}
        })
          .then(response => {
            self.dataPending = response.data.content.reverse()
            self.totalItemInPage = self.dataPending.length
            self.formatDataTable()
          })
      },
      getOrderDelivered() {
        var self = this
        axios.get('admin/order/list/?orderStatus=DELIVERED', {
          headers: {Authorization: self.$store.state.userToken}
        })
          .then(response => {
            self.dataPending = response.data.content.reverse()
            self.totalItemInPage = self.dataPending.length
            self.formatDataTable()
          })
      },
      getOrderCanceled() {
        var self = this
        axios.get('admin/order/list/?orderStatus=CANCELED', {
          headers: {Authorization: self.$store.state.userToken}
        })
          .then(response => {
            self.dataPending = response.data.content.reverse()
            self.totalItemInPage = self.dataPending.length
            self.formatDataTable()
          })
      },

      formatDataTable() {
        var self = this

        var list = []

        self.dataPending.forEach((item, index) => {
          if (self.currentPage === 1) {
            if (index >= 0 && index < parseInt(self.sizePage)) {
              var a = {
                id: item.id,
                name: item.customer.lastName + ' ' + item.customer.firstName,
                email: item.customer.email,
                price: item.totalPrice,
                timeorder: self.formatDateTime(item.orderTime)
              }
              list.push(a)
            }
          } else {
            if ((index <= parseInt(self.currentPage) * parseInt(self.sizePage)) &&
              (index >= (parseInt(self.currentPage) - 1) * parseInt(self.sizePage))) {
              var b = {
                id: item.id,
                name: item.customer.lastName + ' ' + item.customer.firstName,
                email: item.customer.email,
                price: item.totalPrice,
                timeorder: self.formatDateTime(item.orderTime)
              }
              list.push(b)
            }
          }
        })
        self.$set(self.table, 'data', list)
      },
      formatDateTime(val) {
        if (val != null) {
          var rs = new Date(val.toString())
          return rs.toLocaleString()
        }
      },
      setActivationItem(val) {
        var self = this
        self.selectedItem = val
        switch (val) {
          case 'all':
            self.getOrderAll()
            break
          case 'pending':
            self.getOrderPendding()
            break
          case 'ordered':
            self.getOrderOdered()
            break
          case 'confirmed':
            self.getOrderConfirmed()
            break
          case 'delivering':
            self.getOrderDelivering()
            break
          case 'delivered':
            self.getOrderDelivered()
            break
          case 'canceled':
            self.getOrderCanceled()
            break
        }
      },
      clickRow(val) {
        // var self = this
        this.triggerOpenModalOrder = true
        this.orderInfor = val
      },
      closeModalOrder() {
        this.triggerOpenModalOrder = false
      }
    },
    mounted() {
      this.getOrderOdered()
    }
  }
</script>




