<template lang="pug">
  .new-dish
    .icon-center(@click="addNewDish")
      i.material-icons add
      p Thêm mới

</template>

<script>
  export default {
    name: 'md-addnewdish',
    props: {
      categoryId: {
        type: Number
      }
    },
    methods: {
      addNewDish() {
        this.$emit('addnewdish', this.categoryId)
      }
    }
  }
</script>

<style lang="scss"></style>



