import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/Dashboard'
// import UserProfile from '@/components/UserProfile'
// import TableList from '@/components/TableList'
import Menu from '@/components/Menu'
// import Typography from '@/components/Typography'
import Icons from '@/components/Icons'
import Maps from '@/components/Maps'
// import Notifications from '@/components/Notifications'
import ChatWithHuman from '@/components/ChatWithHuman'
import AddNewDishContent from '@/components/AddNewDishContent'
import EditDish from '@/components/EditDish'
import Users from '@/components/Users'
import Customer from '@/components/Customer'
import Order from '@/components/Order'
import Promotion from '@/components/Promotion'
import Feedback from '@/components/Feedback'
import Reservation from '@/components/Reservation'

import Report from '@/components/Report'

// GeneralViews
import NotFound from '@/components/GeneralViews/NotFound.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Dashboard',
      component: Dashboard
    },
    {
      path: '/Dashboard',
      name: 'Dashboard',
      component: Dashboard
    },
    {
      path: '/reservation',
      name: 'Reservation',
      component: Reservation
    },
    {
      path: '/order',
      name: 'Order',
      component: Order
    },
    {
      path: '/menu',
      name: 'Menu',
      component: Menu
    },
    {
      path: '/promotion',
      name: 'Promotion',
      component: Promotion
    },
    {
      path: '/icons',
      name: 'Icons',
      component: Icons
    },
    {
      path: '/maps',
      name: 'Maps',
      component: Maps
    },
    {
      path: '/feedback',
      name: 'Feedback',
      component: Feedback
    },
    {
      path: '/chatwithhuman',
      name: 'Chat with Human',
      component: ChatWithHuman
    },
    {
      path: '/users',
      name: 'Danh Sách Người Dùng',
      component: Users
    },
    {
      path: '/customer',
      name: 'Danh sách khách hàng',
      component: Customer
    },
    {
      path: '/menu/createDish',
      name: 'Tạo mới món ăn',
      component: AddNewDishContent
    },
    {
      path: '/menu/editDish/:id',
      name: 'Chỉnh sửa món ăn',
      component: EditDish
    },
    {
      path: '/report',
      name: 'Report',
      component: Report
    },
    {path: '*', component: NotFound}
  ],
  linkActiveClass: 'active'
})



