<template lang="pug">
.content
    .row
      .col-md-12
        el-tabs(v-model="activeName" @tab-click="handleClick")
          el-tab-pane(label="TO CONFIRM" name="to_confirm")
            .reservation-list
              .reservation-item-wrapper(v-for='item in list_reservation')
                .col-md-10.no-padding
                  .common-info.col-md-12.no-padding
                    .col-md-2.no-padding
                      span.common-info__person-name {{ item.customer.lastName + ' ' + item.customer.firstName}}
                    .col-md-5.no-padding
                      span.common-info__status Waiting store confirmation
                  .detail-info.col-md-12.no-padding
                    .col-md-1.no-padding
                      .detail-info__title ID
                      .detail-info__detail-title {{item.id}}
                    .col-md-2
                      .detail-info__title Date
                      .detail-info__detail-title {{ item.reservationDate|dateTimeFormat}}
                    .col-md-3
                      .detail-info__title Number of people
                      .detail-info__detail-title {{ item.numberOfPeople }}
                    .col-md-6
                      .detail-info__title More information
                      .detail-info__detail-title {{ item.request }}
                .col-md-2.no-padding.text-center
                  button.btn.btn-success.btn-xs(type='button', rel='tooltip', title='Confirm reservation', @click='confirmReservation(item)')
                    i.material-icons check
                  button.btn.btn-danger.btn-xs(type='button', rel='tooltip', title='Delete reservation', @click='delReservation(item)')
                    i.material-icons delete






</template>

<script>
  import axios from 'axios'
  import swal from 'sweetalert'

  export default {
    data() {
      return {
        list_reservation: [],
        activeName: 'to_confirm' // to_confirm and search
      }
    },
    filters: {
      dateTimeFormat: function (value) {
        var today = new Date(value)
        var dd = today.getDate()
        var mm = today.getMonth() + 1

        var yyyy = today.getFullYear()
        if (dd < 10) {
          dd = '0' + dd
        }
        if (mm < 10) {
          mm = '0' + mm
        }
        var todayr = dd + '/' + mm + '/' + yyyy
        return todayr
      }
    },
    methods: {
      confirmReservation(item) {
        // var self = this
        swal({
          title: 'Chú ý?',
          text: 'Bạn sẽ xác nhận thông tin đặt bàn này?',
          icon: 'warning',
          buttons: [
            'Không',
            'Có'
          ]
        })
          .then(ok => {
            if (ok) {
              axios.delete('admin/reservation/delete/' + item.id.toString(), {
                headers: {Authorization: self.$store.state.userToken}
              })
                .then(function (response) {
                  self.delReservationTemp(item.id)
                  swal('Đã xóa!', 'Đã xóa đặt bàn thành công', 'success')
                })
            }
          })
      },
      delReservationTemp(id) {
        var self = this
        var listTemp = self.list_reservation.filter((item) => {
          return item.id !== id
        })
        self.list_reservation = listTemp
      },
      delReservation(item) {
        var self = this
        swal({
          title: 'Cảnh báo?',
          text: 'Bạn có chắc sẽ xóa đặt bàn này? Dữ liệu đã xóa sẽ không phục hồi lại được!',
          icon: 'warning',
          buttons: [
            'Không',
            'Có'
          ],
          dangerMode: true
        })
          .then(willDelete => {
            if (willDelete) {
              axios.delete('admin/reservation/delete/' + item.id.toString(), {
                headers: {Authorization: self.$store.state.userToken}
              })
                .then(function (response) {
                  self.delReservationTemp(item.id)
                  swal('Đã xóa!', 'Đã xóa đặt bàn thành công', 'success')
                })
            }
          })
      },
      handleClick() {
        console.log('click')
      },
      getListReservation() {
        var self = this

        axios.get('admin/reservation/list/', {
          headers: {Authorization: self.$store.state.userToken}
        })
          .then(response => {
            self.list_reservation = response.data.content
          })
      }
    },

    created() {
      this.getListReservation()
    }
  }
</script>
<style lang="scss">
  .reservation-item-wrapper {
    min-height: 100px;
    border-left: 4px solid #6799ef;
    border-radius: 3px;
    box-shadow: 0 0 3px 0px #8c8585;
    margin: 10px;
    margin-top: 20px;
    padding: 10px 20px 10px 20px;
    background-color: #f7f6f6;
  }

  .common-info {
    &__person-name {
      font-size: 16px;
      font-weight: bold;
      color: #333333;
    }

    &__status {
      margin-left: 20px;
      color: white;
      font-size: 12px;
      background-color: #6799ef;
      padding: 5px 5px 2px 5px;
      border-radius: 3px;
    }
  }

  .detail-info {
    margin-top: 10px;

    &__title {
      color: #333333;
      font-weight: 400;
    }

    &__detail-title {
      color: #737373
    }
  }

  .no-padding {
    padding: 0px;
  }

  .text-center {
    text-align: center;
  }

</style>




