<template lang='pug'>
.content
  md-modal(id='modal-create-edit-promotion', v-model='showModal', @closeModal='closeModalPromotion')
    h3(slot='header' class='display-none')
    .row(slot='body')
      .col-md-12
        .card
          .card-header(data-background-color='purple')
            h4.title(v-if="mode==='create'") THÊM MỚI KHUYẾN MÃI
            h4.title(v-else) CHỈNH SỬA KHUYẾN MÃI
          .card-content
            form.max-width
              .row#detail-promotion-header
                .col-md-7
                  .col-md-6
                    md-fg-input(label='Title', :disabled='false', :labelFloating='false', v-model='promotion_infor_temp.title')
                  .col-md-6
                    md-fg-input(label='Subtitle', :disabled='false', :labelFloating='false', v-model='promotion_infor_temp.subTitle')
                  .col-md-6
                    md-fg-input(type='date', label='From', :disabled='false', :labelFloating='false', v-model='promotion_infor_temp.availableFrom')
                  .col-md-6
                    md-fg-input(type='date', label='To', :disabled='false', :labelFloating='false', v-model='promotion_infor_temp.availableTo')
                  .col-md-4
                    md-fg-input(type='number', label='Percent down', :disabled='false', :labelFloating='false', v-model='promotion_infor_temp.percentDown')
                  .col-md-12
                    md-fg-input(type='textarea', label='Mô tả',:rows='4', :disabled='false', :labelFloating='false', v-model='promotion_infor_temp.content')
                .col-md-5
                  .col-md-12
                    p Hình ảnh khuyến mãi
                    .image-avatar
                      label.content-upload(for='upload-image-promote')
                        img.img(:src='imageNewPromote', for='upload-image-promote')
                        md-fg-input-file(id='upload-image-promote', :disabled='false', :labelFloating='false', @uploadFile='handleUploadFile')
                  .col-md-12
                    md-button.btn.pull-right(@click.prevent='closeModalPromotion') Đóng
                    md-button.btn.btn-success.pull-right(@click.prevent='createPromotion' v-if="mode==='create'") Tạo
                    md-button.btn.btn-success.pull-right(@click.prevent='editPromotion' v-else) Chỉnh sửa

              .clearfix






</template>

<script>
  import axios from 'axios'
  import swal from 'sweetalert'

  export default {
    props: {
      triggerShowModal: {
        type: Boolean,
        default: false
      },
      promotionData: {
        type: Object,
        default: {}
      },
      mode: {
        type: String,
        default: 'create'
      }

    },
    data() {
      return {
        // showModal: false,
        promotion_infor_temp: {
          'id': -1,
          'title': '',
          'imageUrl': '',
          'subTitle': '',
          'url': '',
          'content': '',
          'availableFrom': '',
          'availableTo': '',
          'percentDown': 0
        },
        imagePromote: '',
        imagePromoteDefault: 'https://restaurantchatbot.s3.amazonaws.com/1541085716573-noimage.jpg'
      }
    },
    computed: {
      imageNewPromote() {
        if (this.imagePromote !== '') {
          return this.imagePromote
        } else {
          return this.imagePromoteDefault
        }
      },
      showModal() {
        return this.triggerShowModal
      }
    },
    watch: {
      triggerShowModal: function (val) {
        var self = this
        if (val) {
          if (self.mode === 'create') {
            // create
            self.resetDataTemp()
          } else {
            // edit
            self.setDataTempToEdit(self.promotionData)
          }
        }
      }
    },
    methods: {
      editPromotion() {
        var self = this

        var fromDate = self.convertToISOtringDate(self.promotion_infor_temp.availableFrom)
        var toDate = self.convertToISOtringDate(self.promotion_infor_temp.availableTo)
        console.log(fromDate)
        console.log(toDate)
        axios.put('/admin/promotion/update/' + self.promotion_infor_temp.id, {
          'availableFrom': fromDate,
          'availableTo': toDate,
          'content': self.promotion_infor_temp.content,
          'imageUrl': self.imagePromote,
          'percentDown': self.promotion_infor_temp.percentDown,
          'subTitle': self.promotion_infor_temp.subTitle,
          'title': self.promotion_infor_temp.title,
          'url': self.promotion_infor_temp.url
        }, {
          headers: {Authorization: self.$store.state.userToken}
        })
          .then(function (response) {
            swal('Thành công!', 'Đã chỉnh sửa thành công!', 'success')
          })
      },
      createPromotion() {
        var self = this

        var fromDate = self.convertToISOtringDate(self.promotion_infor_temp.availableFrom)
        var toDate = self.convertToISOtringDate(self.promotion_infor_temp.availableTo)

        axios.post('/admin/promotion/add', {
          'availableFrom': fromDate,
          'availableTo': toDate,
          'content': self.promotion_infor_temp.content,
          'imageUrl': self.imagePromote,
          'percentDown': self.promotion_infor_temp.percentDown,
          'subTitle': self.promotion_infor_temp.subTitle,
          'title': self.promotion_infor_temp.title,
          'url': self.promotion_infor_temp.url
        }, {
          headers: {Authorization: self.$store.state.userToken}
        })
          .then(function (response) {
            swal('Thành công!', 'Đã thêm mới thành công!', 'success')
          })
      },
      convertToISOtringDate(date) {
        // var self = this
        var darr = date.split('-')   // ['29', '1', '2016']
        var dobj = new Date(parseInt(darr[0]), parseInt(darr[1]) - 1, parseInt(darr[2]))
        // Date {Fri Jan 29 2016 00:00:00 GMT+0530(utopia standard time)
        return dobj.toISOString()
      },
      convertFromISOStringDateToDateNormal(val) {
        var date = new Date(val)
        var dd = date.getDate()
        var mm = date.getMonth() + 1

        var yyyy = date.getFullYear()
        if (dd < 10) {
          dd = '0' + dd
        }
        if (mm < 10) {
          mm = '0' + mm
        }
        var rs = yyyy + '-' + mm + '-' + dd
        return rs
      },
      setDataTempToEdit(val) {
        var self = this
        var temp = Object.assign({}, val)
        temp.availableFrom = self.convertFromISOStringDateToDateNormal(val.availableFrom)
        temp.availableTo = self.convertFromISOStringDateToDateNormal(val.availableTo)
        self.promotion_infor_temp = temp
        this.imagePromote = self.promotion_infor_temp.imageUrl
      },
      resetDataTemp() {
        var temp = {
          'id': -1,
          'title': '',
          'imageUrl': '',
          'subTitle': '',
          'url': '',
          'content': '',
          'availableFrom': '',
          'availableTo': '',
          'percentDown': 0
        }
        this.promotion_infor_temp = temp
        this.imagePromote = ''
      },
      handleUploadFile(val) {
        var self = this
        let formData = new FormData()
        formData.append('file', val)
        axios.post('/upload',
          formData,
          {
            headers: {
              'Content-Type': 'multipart/form-data',
              Authorization: self.$store.state.userToken
            }
          })
          .then(response => {
            self.imagePromote = response.data
          })
      },
      confirm() {

      },
      closeModalPromotion() {
        this.$emit('closeModalPromotion')
      },
      calcTotalPrice(quantity, price) {
        return (parseFloat(quantity) * parseFloat(price)).toFixed(0)
      }
    }
  }
</script>
<style lang='sass'>

</style>




