// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import Vuex from 'vuex'

// Libraries
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import './assets/sass/material-dashboard.scss'
import 'es6-promise/auto'
import Chartist from 'chartist'
import CoreComponents from '@/core/components'
import NotificationPlugin from '@/core/components/mdNotifications/install'
import vClickOutside from 'v-click-outside'
import Element from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'

import axios from 'axios'
import VueAxios from 'vue-axios'

window.$q = require('jquery')
window.JQuery = require('jquery')

axios.defaults.baseURL = 'https://linhnguyen.me'

Vue.config.productionTip = false

// global library setup
Object.defineProperty(Vue.prototype, '$Chartist', {
  get() {
    return this.$root.Chartist
  }
})

// Use Core Components
Vue.use(CoreComponents)
Vue.use(NotificationPlugin)
Vue.use(vClickOutside)
Vue.use(VueAxios, axios)
Vue.use(Vuex)
Vue.use(Element)

Vue.use(require('@websanova/vue-auth'), {
  auth: require('@websanova/vue-auth/drivers/auth/bearer.js'),
  http: require('@websanova/vue-auth/drivers/http/axios.1.x.js'),
  router: require('@websanova/vue-auth/drivers/router/vue-router.2.x.js')
})

const store = new Vuex.Store({
  state: {
    userToken: '',
    userName: '',
    userInfo: {}
  },
  mutations: {
    setUserToken(state, val) {
      state.userToken = val
    },
    setUserName(state, val) {
      state.userName = val
    },
    setUserInfo(state, val) {
      state.userInfo = val
    }
  }
})

/* eslint-disable no-new */
new Vue({
  el: '#content',
  router,
  store,
  template: '<App/>',
  components: {App},
  data: {
    Chartist
  }
})




