<template lang="pug">
.content
  .mg-r-10.md-button.btn.btn-primary(@click='addNewUser') Thêm mới người dùng

  md-modal(id='modal-new-user', v-model='showModal', @closeModal='closeModalNewUser')
    h3(slot='header' class='display-none') Thông tin người dùng
    .row(slot='body')
      .col-md-12
        .card
          .card-header(data-background-color='purple')
            h4.title Thêm mới người dùng
          .card-content
            form.max-width
              .row
                .col-md-4
                  md-fg-input(label='Username', :disabled='false', :labelFloating='false', v-model='user.userName')
                .col-md-4
                  md-fg-input(label='Password', :disabled='false', :labelFloating='false', v-model='user.password')
                .col-md-4
                  md-fg-input(label='Email', :disabled='false', :labelFloating='false', v-model='user.email')
              .row
                .col-md-12
                  md-fg-input(label='Adress', :disabled='false', :labelFloating='false', v-model='user.address')
              .row
                .col-md-6
                  md-fg-input(label='Phone', :disabled='false' :labelFloating='false', v-model='user.phoneNumber')
                .col-md-6
                  .col-md-12
                    span role
                  .col-md-6
                    input(type='radio',value='1', id='one', v-model='user.roles[0].id')
                    label(for='one') Admin
                  .col-md-6
                    input(type='radio',value='2', id='two', v-model='user.roles[0].id')
                    label(for='two') Staff

              md-button.btn.pull-right(@click.prevent='closeModalNewUser') Đóng
              md-button.btn.btn-success.pull-right(@click.prevent='saveProfile') OK
              .clearfix
  md-table(class='table-user', type='hover', :data='table.data', :columns='table.columns')

</template>
<script>

  import axios from 'axios'
  import swal from 'sweetalert'

  export default {
    data() {
      return {
        tempresponsedata: {
          'content': [],
          'pageable': {
            'sort': {
              'sorted': false,
              'unsorted': true
            },
            'pageSize': 20,
            'pageNumber': 0,
            'offset': 0,
            'paged': true,
            'unpaged': false
          },
          'totalPages': 1,
          'totalElements': 4,
          'last': true,
          'first': true,
          'sort': {
            'sorted': false,
            'unsorted': true
          },
          'numberOfElements': 4,
          'size': 20,
          'number': 0
        },
        table: {
          columns: ['ID', 'USERNAME', 'ADDRESS', 'EMAIL', 'DETAIL'],
          data: []
        },
        user: {
          id: 0,
          userName: '',
          password: '',
          address: '',
          email: '',
          phoneNumber: '',
          roles: [{
            id: 1,
            roleName: 'One',
            description: 'string'
          }]
        },
        showModal: false,
        alluser: {}
      }
    },
    methods: {
      saveProfile() {
        var self = this
        axios.post('/users/sign-up', {
          'address': self.user.address,
          'email': self.user.email,
          'password': self.user.password,
          'phoneNumber': self.user.phoneNumber,
          'userName': self.user.userName
        }, {
          headers: {Authorization: self.$store.state.userToken}
        })
          .then(function (response) {
            swal('Thành công!', 'Đã thêm thành công!', 'success')
            self.closeModalNewUser()
          })
      },
      closeModalNewUser() {
        this.showModal = false
      },
      oprenModalNewUser() {
        this.showModal = true
      },
      addNewUser() {
        this.oprenModalNewUser()
      },
      getAllUser() {
        var self = this
        var token = window.localStorage.getItem('user_token')

        return axios.get('/users/list/?offset=0&pageNumber=0&pageSize=10',
          {
            headers: {
              'Content-Type': 'multipart/form-data',
              Authorization: token
            }
          })
          .then(response => {
            self.alluser = response.data
          })
      },
      formatDataUser() {
        var self = this
        // var test = []
        var rootData = Object.assign({}, self.alluser)
        if (rootData.content) {
          var res = rootData.content.map((cus, index) => {
            if (cus.roles) {
              return {
                'id': cus.id,
                'username': cus.userName,
                'address': cus.address,
                'email': cus.email,
                'phoneNumber': cus.phoneNumber,
                'role': cus.roles.roleName,
                'detail': '<a class="icon-view-detail-user" href="javascript:void(0)" onclick=""><i class="fas fa-info-circle"></i></a>'
              }
            }
          })
          this.vailua = res
          this.table.data = res
        }
      }
    },
    mounted() {
      this.getAllUser().then(res => {
        this.formatDataUser()
      })
    }
  }

</script>



